<template>
  <div id="edit-note">
    <head-top title="编辑笔记">
      <span slot="right" @click="saveData">
        <i class="iconfont icon-finish"></i>
      </span>
    </head-top>

    <div class="main">
      <div class="tt">
        <input type="text" placeholder="编辑笔记标题" v-model="title" maxlength="50" />
      </div>
      <r-quill-editor
        :funType="funType"
        @change="editorChange"
        max-height="calc(100vh - 4.5rem - 4rem - 42px)"
      ></r-quill-editor>
    </div>
  </div>
</template>

<script>
import headTop from "@/components/headTop";
import { Uploader } from "vant";
import RQuillEditor from "@/components/RQuillEditor";

import { ModulePicture } from "@/utils/enum";
export default {
  name: "",
  components: { headTop, [Uploader.name]: Uploader, RQuillEditor },
  props: {},
  data() {
    return {
      title: "",
      content: "",
      imgBase: "",
      funType: ModulePicture.StudentNote,
      saveLoading: false
    };
  },
  created() {},
  mounted() {
  },
  methods: {
    saveData() {
      if (this.saveLoading) return;
      this.saveLoading = true;
      if (!this.title) {
        this.$toast("笔记标题不能为空");
        this.saveLoading = false;
        return;
      }
      if (!this.content) {
        this.$toast("笔记内容不能为空");
        this.saveLoading = false;
        return;
      }

      this.$gatewayHttp.note
        .postSaveEdit({
          courseID: this.$store.getters.courseID,
          studentID: this.$store.getters.studentID,
          snTitle: this.title,
          snContent: this.content
        })
        .then(res => {
          if (res.Result) {
            this.$toast.success("添加笔记成功");
            this.$router.push("/note");
          } else if (res.Info) {
            this.$toast(res.Info);
          }
          this.saveLoading = false;
        })
        .catch(err => {
          this.$toast("添加失败，请重试");
          this.saveLoading = false;
        });
    },

    editorChange(value) {
      this.content = value;
    }
  }
};
</script>

<style lang='less' scoped>
#edit-note {
  width: 100%;
  height: 100%;
  background: #ebf0ef;
  .icon-finish {
    float: right;
    margin-right: 4vw;
    line-height: 4.3rem;
    color: #fff;
    font-size: 1.5rem;
  }
  .main {
    text-align: left;
    background: #ebf0ef;

    .tt {
      width: 92vw;
      padding-left: 4vw;
      padding-right: 4vw;
      color: #999;
      height: 4rem;
      line-height: 4rem;
      background: #fff;
      input {
        color: #999;
        height: 3.5rem;
        line-height: 3.5rem;
        width: 100%;
      }
    }
  }
}
</style>


